import { Component, AfterViewInit, OnInit, ViewEncapsulation, OnChanges } from '@angular/core';
import { JSONBuilder } from '../../../services/JSONBuilder.service';
import { FormulaService } from '../../../services/formula.service';
import { EditorLeadForm } from '../../editors/leadform/editor_leadform.component';
import { AddSection } from './component/addsection.component';
import { BuilderService } from '../../../services/builder.service';
import { JSONItemTracker } from '../../../services/JSONUpdateItemTracker.service';
import { Item, Section, Page } from '../../../models/model';
import { FeatureAuthService } from '../../../../../shared/services/feature-access.service';
import { RecommendationService } from '../../../../templates/services/recommendation.service';
import { EditorWysiwyg } from './../../editors/wysiwyg/editor_wysiwyg.component';
import { environment } from "../../../../../../environments/environment";
import { ShareOutcomeService } from './../../../services/shareOutcome.service';
import { ConditionalResultService } from '../../../services/conditionalResult.service';

declare var jQuery: any;
declare var math: any;
declare var filepicker: any;
declare var window: any;
declare var ga: any;
declare var _kmq: any;
declare var FB: any;
